@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
:root {
  --font-family: 'Manrope', sans-serif;

  --gradient-text: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
  --gradient-text-reverse: linear-gradient(89.97deg, #F49867 1.84%, #AE67FA 50.67%);
  --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
  
  --color-bg: #040C18;
  --color-footer : #031B34;
  --color-blog: #042c54;
  --color-text: #81AFDD;
  --color-subtext: #FF8A71;
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior:smooth;
  }
  
  body {
    background: var(--color-bg);
    font-family: var(--font-family);
    
  }
  
  a {
    color: unset;
    text-decoration: none;
  }
  
  .gradient__bg {
      
    /* safari 5.1+,chrome 10+ */
  
    /* opera 11.10+ */
  
    /* ie 10+ */
   background:-ms-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 25%);
  


/* global 90%+ browsers support */
background:conic-gradient(from 90deg at 0% 100%, rgba(0, 0, 0, 1) 98%, rgba(7, 58, 187, 1) 100%);


}
  
  
  .gradient__text {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
  }
  
  .section__padding {
    padding: 4rem 6rem;
  }
  
  .section__margin {
    margin: 4rem 6rem;
  }
  
  .scale-up-center {
      animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
  }
  h1,
h2,
h3,
h4 {
    color: var(--title-color);
    font-weight: var(--font-semi-bold);
}

ul {
    list-style: none;
}
@media  (max-width: 400px) {
  .section__padding {
      padding: 1rem;
      }
  .section__margin{
    margin: 1rem;
  }
}
img {
    min-width: 100%;
    height: auto;
}
