.intro {
    display: block;
    padding: 50px;
    align-items: center;
    height: 82.5vh;
    background: -ms-radial-gradient(circle at 3% 25%, rgba(0, 40, 83, 1) 0%, rgba(4, 12, 24, 1) 25%);
    background: conic-gradient(from 90deg at 0% 100%, rgba(0, 0, 0, 1) 98%, rgba(7, 58, 187, 1) 100%);
}

.intro h1 {
    font-family: 'Poppins', sans-serif;
    font-size: 4vw;
    width: 90%;
    font-weight: 900;
    margin-top: -50px;
    margin-left: 80px;
    color: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);
    color: rgb(243, 235, 235);
    -webkit-test-stroke: 2px rgb(7, 7, 7);

}

.intro a {
    background-color: transparent;
    color: var(--color-subtext);
    width: 15vw;
    margin-left: 80px;
    font-size: 1.2vw;
    font-weight: 600;
    margin-top: 20px;
    border: 1px solid var(--color-subtext);
    box-shadow: inset 0 0 0 0 var(--first-color-alt);
    transition: ease-out 0.15s;
    outline: none;
    transition: .15s;
}

.intro a:hover {
    box-shadow: inset 30vw 0 0 0 var(--color-subtext);
    color: white;
    outline: none;
    font-weight: 900;
    transition: .5s;
    /* box-shadow: inset 0 0 0 0 var(--first-color-alt); */
}

.name {
    font-size: 100px;
    color: transparent;
    -webkit-text-stroke: 1px #FFF;
    background: url(data:image/png;base64,);
    -webkit-background-clip: text;
    background-position: 0 0;
    animation: back 20s linear infinite;
    text-transform: uppercase;
    /* background:linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%) ; */
}

@keyframes back {
    100% {
        background-position: 5000px 0;

    }
}

@media (max-width: 800px) {
    .intro {
        padding: 20px 20px;
        /* height: 80vh; */
        max-height: -webkit-max-content;
        max-height: max-content;
        line-height: 50px;
        /* width: 100vh; */
    }

    .intro h1 {
        font-size: 5vh;
        width: 100%;
        margin-top: 50px;
        margin-left: 20px;
        /* text-align: center; */
    }

    .name {
        font-size: 60px;
        margin-bottom: 50px;
    }

    .intro a {
        /* text-align: center; */
        width: 80vw;
        margin-left: 20px;
        font-size: 30px;
    }
}
