.navbar{
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: -webkit-sticky;
    position: sticky;
    padding: 1.3rem 6rem;
}
.navbar-links{
    display: flex;
    flex: 1 1;
    justify-content: flex-start;
    align-items: center;
}
.navbar-links-logo{
    margin-right: 2rem;
    margin-top: -10px;
}
.navbar-links-logo img{
    width: 70px;
    height: 70px;
}

.navbar-links__container{
    display: flex;
    flex-direction: row;

}
.navbar-sign{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.navbar-links__container p,
.navbar-sign p,
.navbar-links-logo p,
.navbar-menu__container p{
    color: #fff;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}
.navbar-links-logo p{
    font-weight: 600;
    font-size:28px ;
    text-align: center;
    justify-content: center;
}
.navbar-sign button,
