    padding: 0.5rem 1rem;
    color: #fff;
    background: #FF4820;
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border: none;
    outline: none;
    cursor: pointer;
    border-radius: 5px;
}
.navbar-menu{
    margin-left: 1rem;
    /* display: none; */
    position: relative;
}
.navbar-menu svg{
    cursor: pointer;
}
.navbar-menu__container{
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;
    
    text-align: end;
    background: transparent;
    /* padding: 2rem; */
    position: absolute;
    right: 0;
    top: 40px;
    /* margin-top: 1rem; */
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0px 0px 5px rgba(0,0, 0,0.2);


}
.navbar-menu__container p{
    margin: 1rem 0;
}
.navbar-menu_container-links-sign {
    display: none;
}
